.popup-duan {
  width: 200px;
  padding: 10px;
  background: #fff;
  border-radius: 5px;

  &__image {
    width: 100%;
    height: 100px;
    border-radius: 5px;
    object-fit: cover;
  }

  &__title {
    font-size: 14px;
    margin: 8px 0;
    font-weight: bold;
  }

  p {
    font-size: 12px;
    margin: 4px 0;
  }

  &__link {
    color: blue;
    text-decoration: underline;
    display: block;
    margin-top: 8px;
    text-align: right;
  }
}

@media (max-width: 480px) {
  .popup-duan {
    width: 160px;
    padding: 8px;

    &__image {
      height: 80px;
    }

    &__title {
      font-size: 12px;
    }

    p {
      font-size: 11px;
    }
  }
}

.popup-quyhoach {
  width: 220px;
  padding: 10px;
  background: #fff;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__thumbnail {
    width: 100%;
    height: auto;
    border-radius: 5px;
    object-fit: cover;
  }

  &__video {
    width: 100%;
    cursor: pointer;
  }

  &__image360 {
    width: 100%;
    cursor: pointer;

    &-thumbnail {
      width: 100%;
      height: auto;
      border-radius: 5px;
    }
  }

  &__description {
    font-size: 14px;
    margin: 8px 0;
    font-weight: bold;
  }

  &__button--share {
    display: flex;
    align-items: center;
    gap: 6px;
    background-color: #007bff;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 10px;

    &:hover {
      background-color: #0056b3;
    }

    svg {
      font-size: 16px;
    }
  }
}

@media (max-width: 480px) {
  .popup-quyhoach {
    width: 180px;
    padding: 10px;

    &__description {
      font-size: 12px;
    }

    &__button--share {
      font-size: 12px;
      padding: 6px 10px;
    }
  }
}