.history-cost-container {
  position: fixed;
  top: 65px;
  right: 100px;
  width: 60%;
  background-color: #1f252a;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 16px;
  border-radius: 8px;
  z-index: 1000;

  .close-btn {
    position: absolute;
    top: 8px;
    right: 8px;
    color: #ff0000;
    cursor: pointer;
    font-weight: bold;
    font-size: 18px;
    line-height: 1;
  }

  .address-box {
    margin: 15px;
    padding: 12px;
    background-color: #2c333a;
    border-radius: 8px;
    font-size: 16px;
    line-height: 1.5;

    .address-text {
      color: #fff;
    }
  }

  .tabs-container {
    margin: 16px 62px;
    display: flex;
    gap: 8px;
    white-space: nowrap;
    padding-bottom: 8px;

    .tab-item {
      position: relative;
      display: inline-block;

      .tab-btn {
        padding: 8px 12px;
        cursor: pointer;
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 4px;
        color: #00bfff;

        &.active {
          background-color: #00bfff;
          color: #fff;
        }
      }

      .tab-popup {
        position: absolute;
        top: 110%;
        left: 50%;
        transform: translateX(-50%);
        background-color: #fff;
        color: #000;
        padding: 8px;
        border-radius: 4px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        z-index: 2000;
        width: 200px;
        overflow: visible;
        white-space: normal;

        img {
          width: 100%;
          height: auto;
          max-height: 100px;
          object-fit: cover;
          border-radius: 4px;
          margin-bottom: 8px;
        }

        p {
          margin: 0;
          font-size: 12px;
        }
      }
    }
  }

  .loading {
    color: #fff;
    text-align: left;
  }
}