.estate-price-container {
  position: fixed;
  top: 65px;
  right: 100px;
  width: 60%;
  max-height: 85vh; 
  background-color: #1f252a;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 16px;
  border-radius: 8px;
  z-index: 1000;

  .close-btn {
    position: absolute;
    top: 8px;
    right: 8px;
    color: #ff0000;
    cursor: pointer;
    font-weight: bold;
    font-size: 18px;
    line-height: 1;
  }

  .address-box {
    margin: 15px;
    padding: 14px;
    background-color: #2c333a;
    border-radius: 8px;
    font-size: 14px;
    line-height: 1.5;

    .address-text {
      color: #fff;
    }
  }

  .tabs-container {
    margin: 10px 35px;
    display: flex;
    gap: 8px;
    white-space: nowrap;
    padding-bottom: 8px;

    .tab-item {
      position: relative;
      display: inline-block;

      .tab-btn {
        padding: 8px 14px;
        cursor: pointer;
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 4px;
        color: #00bfff;
        font-size: 14px;

        &.active {
          background-color: #00bfff;
          color: #fff;
        }
      }

      .tab-popup {
        position: absolute;
        top: 110%;
        left: 50%;
        transform: translateX(-50%);
        background-color: #fff;
        color: #000;
        padding: 8px;
        border-radius: 4px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        z-index: 2000;
        width: 200px;
        overflow: visible;
        white-space: normal;

        img {
          width: 100%;
          height: auto;
          max-height: 100px;
          object-fit: cover;
          border-radius: 4px;
          margin-bottom: 8px;
        }

        p {
          margin: 0;
          font-size: 14px;
        }
      }
    }
  }

  .loading {
    color: #fff;
    text-align: left;
  }

  .table-container {
    overflow-y: auto;
    font-size: 12px;
    max-height: 58vh; 

    table {
      table-layout: fixed;
      width: 100%;
      border-collapse: collapse;
      background-color: #2c333a;
      color: #fff;
      border-radius: 8px;
      overflow: hidden;
    }

    th,
    td {
      padding: 14px 12px;
      text-align: center;
      border-bottom: 1px solid #444;
      max-width: 100px;
      height: 40px; 
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 200px;
    }

    th:first-child,
    td:first-child {
      width: 60px; 
    }

    th {
      background-color: #00bfff;
      color: #fff;
      font-weight: bold;
    }

    tbody tr:hover {
      background-color: #3a4148;
    }

    a {
      color: #00bfff;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}