// common

.user-select-none {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.image-full {
    width: 100%;
    cursor: pointer;
}
.content-full {
    width: 100% !important;
    height: 100% !important;
}

.container-header {
    top: auto !important;
    bottom: 70px;
}
@media (min-width: 768px) {
    .container-header {
        top: 0 !important;
        bottom: unset;
    }
}

.loading-screen {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgb(255, 255, 255, 0.3);
    z-index: 999999;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    .loading-screen__icon {
    }
}
.adsbygoogle {
    position: relative;
    z-index: 1;
    overflow: hidden;
    margin: 0 auto; /* Cn gia qung co */
}
.banner__container {
    .banner__container--img {
        width: 100%;
        height: 500px;
    }
}
._L7jnu {
    background-color: #2c353d !important;
}
._1XM4Z {
    color: #fff;
    background-color: #2c353d !important;
}
.image-banner {
    height: 360px;
    object-fit: fill;
    width: 100%;
    @media (max-width: 768px) {
        height: 100%;
    }
}

.ant-drawer {
    z-index: 1001;
}

