* {
    font-family: Source Sans Pro;
}

.auction-container {
    .react-datepicker-wrapper {
        width: 100%;
    }
    .icon-exit {
        color: #fff;
        font-size: 35px;
        cursor: pointer;
    }
    .icon-exit:active {
        color: #d3bbbb;
    }
    .auction-container-form {
        width: 888px;
        top: -10px;
        padding: 10px 0px 0px 0px;
        gap: 10px;
        display: flex;
        justify-content: center;
        margin-left: 20%;
        margin-right: 30%;
        .text-auction {
            padding: 8px;
            display: flex;
            width: 530px;
            gap: 10px;
            background-color: #262d34;
            border-radius: 20px;
            justify-content: center;
            text-transform: uppercase;
            align-content: center;
        }
        .icon-auction svg {
            width: 60px;
            margin-right: 10px;
        }
        .text-auction h2 {
            color: #b7a800;
            font-size: 38px;
            font-weight: 600;
            text-align: center;
            margin: 0px;
        }
    }
    .auction-content {
        width: 100%;
        display: flex;
        gap: 0px;
        margin-top: 10px 0px;
        padding: 10px 0px;
        gap: 8px;
        .auction-map {
            width: 30%;
            border: 2px solid #000;
            background: #1e1e1e;
        }
        .auction-map img {
            width: 100%;
        }
        .auction-form {
            width: 70%;
            background-color: #262d34;
            padding: 10px;
            border-radius: 20px;
            color: #fff;
            .btn-search {
                text-align: center;
                gap: 8px;
                .btn-search__reset {
                    margin-left: 8px;
                }
            }
            .btn {
                background: #10b700;
            }
            input[type='number'] {
                background-color: #fff;
            }
        }
    }
    .search-results {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-bottom: 50px;
        .text-search {
            padding: 12px;
            display: flex;
            width: 460px;
            gap: 10px;
            justify-content: center;
            color: #fff;
            text-transform: uppercase;
        }
        .list-search-result {
            display: flex;
            flex-direction: column;
            color: #000;
            height: 600px;
            width: 100%;
            .search-container {
                display: flex;
                margin: 5px 0;
                background: #fff;
                border-radius: 20px;
                padding: 10px;
                width: 100%;
                gap: 10px;
                cursor: pointer;
                .description-search {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    p {
                        color: #000;
                    }
                }
                .img-search {
                    display: flex;
                    width: 50%;
                    gap: 0px;
                    margin-right: 30px;
                }
                .img-search img {
                    border-radius: 20px;
                    margin-right: 5px;
                    width: 220px;
                    height: 200px;
                }
            }

            p {
                display: flex;
                color: #fff;
                justify-content: center;
            }
        }
        .pagination-container {
            margin: 5px;

            span,
            a {
                color: #fff;
            }
            .ant-pagination-item-active,
            .ant-pagination-item,
            .ant-select-selector {
                background-color: #63c098;
            }
            .ant-pagination-item-active {
                background-color: #777373;
            }
            .ant-pagination-item-ellipsis {
                color: rgba($color: #acaaaa, $alpha: 0.5);
            }
        }
    }
    .auction-infor-content {
        margin-top: 30px;
        color: #fff;
        .result-infor {
            display: flex;
            flex-direction: column;
            gap: 15px;
            h4 {
                margin-bottom: 20px;
            }
            .tablet-note-auction {
                width: 100%;
                min-height: 50px;
                background-color: #fff;
                border-radius: 10px;
                p {
                    margin: 5px;
                    padding: 5px;
                    color: #000;
                    span {
                        font-weight: 600;
                    }
                }
            }
            .content-infor {
                .infor-item {
                    margin: 10px 0;
                    h5 {
                        text-transform: uppercase;
                        opacity: 0.5;
                    }
                    p {
                        margin: 0 30px;
                        padding: 10px;
                        span {
                            margin-left: 30px;
                        }
                    }
                    .box-comment {
                        margin: 20px;
                        textarea {
                            width: 100%;
                            height: 90px;
                            background-color: #262d34;
                            border: none;
                            box-shadow: 5px 1px 15px #000;
                            border-radius: 20px;
                            outline: none;
                            padding: 10px;
                            color: #fff;
                            resize: none;
                            vertical-align: top;
                        }
                    }
                }
                .news-board {
                    margin-top: 30px;
                    .auction-results-table {
                        table-layout: auto;
                        width: 100%;
                        color: #000;
                        .auction-results-thead {
                            font-weight: bold;
                            background-color: #cbc4c4;
                            line-height: 20px;
                            font-size: 14px;
                        }
                        .auction-result-row-thead th {
                            border: 1px solid #9c9797;
                            padding: 8px 16px;
                            text-transform: uppercase;
                            text-align: center;
                        }
                        .auction-result-row-body {
                            border: 1px solid #9c9797;
                            background-color: #fff;
                        }
                        .auction-result-row-body td {
                            border: 1px solid #9c9797;
                            padding: 8px 0;
                            text-align: center;
                            font-size: 15px;
                        }
                    }
                }
                .time-auction {
                    display: flex;
                    gap: 10px;
                    margin-top: 30px;
                    p {
                        padding: 0 10px;
                        font-size: 16px;
                    }
                }
                .condition-auction {
                    padding: 0 10px;
                    display: flex;
                    flex-direction: column;
                    p {
                        margin-bottom: 5px;
                    }
                    span {
                        font-weight: bold;
                        text-indent: 20px;
                    }
                }
                .link-auction {
                    opacity: 0.7;
                    width: 500px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                .image-slider {
                    padding: 20px;
                    width: 100%;
                    background-color: #262d34;
                    border-radius: 20px;
                    height: 230px;
                    .slider-item {
                        border: 1px solid #000;
                        border-radius: 20px;
                        img {
                            border-radius: 20px;
                            height: 188px;
                        }
                    }
                    .slick-prev {
                        left: -15px;
                    }
                    .slick-next {
                        right: -15px;
                    }
                    .slick-track {
                        display: flex;
                        gap: 20px;
                    }
                }
                .map-video-infor {
                    display: flex;
                    margin: 30px 0;
                    padding: 10px;
                    width: 100%;
                    height: 300px;
                    .map-container {
                        width: 50%;
                        background-color: #10b700;
                        border: 2px solid #000;
                    }
                    .video-container {
                        width: 50%;
                    }
                }
            }
        }
    }
    .btn {
        width: 100px;
        background-color: #10b700;
        margin-left: auto;
        margin-right: auto;
    }
    .infortest p {
        height: 800px;
    }
}

//responsite web
//mobile
@media screen and (max-width: 767px) {
    //search auction
    .auction-content {
        .auction-map {
            display: none;
        }
        .auction-form {
            width: 100% !important;
        }
        .row {
            flex-direction: column;
        }
    }
    .container .auction-container-form {
        margin: 0;
        width: 100%;
        .text-auction h2 {
            font-size: 25px;
        }
        .icon-auction svg {
            width: 30px;
            height: 30px;
        }
    }
    .col-form-label {
        width: 100%;
    }
    .col-sm-8 {
        width: 100%;
    }
    .col-sm-10,
    .col-sm-6 {
        width: 100%;
    }
    .btn-search {
        margin-left: 180px;
    }
    .search-container {
        width: 100% !important;
        display: block !important;
        .description-search {
            width: 100%;
        }
        .img-search img {
            display: none;
        }
    }

    //Infor
    .auction-results-table {
        display: flex;
        border-collapse: collapse;
        .auction-result-row-thead,
        .auction-result-row-tbody {
            display: flex !important;
            flex-direction: column;
        }
    }
    .image-slider {
        height: 380px !important;
        img {
            height: 340px !important;
        }
    }
    .map-video-infor {
        display: flex;
        flex-direction: column;
        height: 700px !important;
        .map-container,
        .video-container {
            width: 100% !important;
            height: 350px;
        }
    }
}
//tablet
@media screen and (min-width: 767px) and (max-width: 1023px) {
    //search auction
    .auction-content {
        .auction-map {
            display: none;
        }
        .auction-form {
            width: 100% !important;
        }
    }
    .container .auction-container-form {
        padding-right: 145px;
        margin: 0;
    }
    .mb-2.row {
        flex-direction: column;
    }
    .col-sm-8 {
        width: 100%;
    }
    .col-form-label {
        width: 100%;
    }
    .search-container {
        width: 100% !important;
        display: block !important;
        .img-search img {
            display: none;
        }
    }

    //infor
}
//pc
@media screen and (min-width: 1024px) {
    .search-container {
        width: 100% !important;
        .img-search img {
            width: 150px !important;
            height: 180px !important;
        }
    }
}
.auctions {
    .ant-tabs-nav-list {
        background-color: #262d34;
    }
    .ant-tabs-tab-btn {
        color: #fff;
        font-size: 1rem;
        font-weight: 600;
    }
    .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
        color: #10b700;
        text-shadow: 0 0 0.25px currentcolor;
    }
    .ant-tabs .ant-tabs-ink-bar {
        position: absolute;
        background: #10b700;
        pointer-events: none;
    }
    .ant-tabs .ant-tabs-tab:hover {
        color: #10b700;
    }
    .ant-pagination .ant-pagination-item-active {
        font-weight: 600;
        background-color: #040607;
        border-color: #777e82;
        color: #fff;
    }
    .ant-pagination .ant-pagination-item,
    .ant-pagination-item-ellipsis,
    .anticon {
        color: #fff;
    }
    .ant-pagination .ant-pagination-item:not(.ant-pagination-item-active):hover {
        background-color: rgba(0, 0, 0, 0.16);
    }
}
