// App.scss
.profile {
  display: flex;
  min-height: 100vh;

  .main-content {
    flex-grow: 1;
    padding: 2px;
  }
}

// Sidebar.scss
.sidebar {
  width: 240px;
  background-color: #262D34;
  padding: 20px;

  button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &.active {
      background-color: #007bff;
      color: #fff;
      @media (max-width: 768px) {
        font-size: 12px;
        
      }
    }

    &:hover {
      background-color: #e0e0e0;
    }
    @media (max-width: 768px) {
      font-size: 12px;
      
    }
  }
}

// EditProfile.scss
.edit-profile {
  max-width: 500px;
  margin: 0 auto;

  h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  .form-group {
    margin-bottom: 15px;

    label {
      display: block;
      margin-bottom: 5px;
    }

    input, textarea, select {
        width: 100%;
        padding: 8px;
        background-color: #2C353D;
      color: #666;
      border-radius: 4px;
    }

    textarea {
      height: 100px;
    }
  }

  button[type="submit"] {
    display: block;
    width: 70%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    &:hover {
      background-color: #0056b3;
    }
  }
}