$fontSizeHeader: 24px;
$fontSize: 16px;

.detail-container {
    @media (max-width: 768px) {
        text-align: center !important;
        margin-right: 10px !important;
        margin-left: 10px !important;

        .detail-image {
            height: 35vh !important; // Gim chiu cao trn mn nh hn
            img {
                width: 80% !important;
                height: 100% !important; // Khng vt qu chiu cao container // M rng nh trn mobile
            }
        }
    }

    @media (max-width: 480px) {
        margin-right: 5px !important;
        margin-left: 5px !important;

        .detail-image {
            text-align: center !important;
            height: 35vh !important;

            img {
                width: 80% !important;
                height: 100% !important; // nh full chiu rng trn in thoi nh
            }
        }
    }
    .detail-image {
        text-align: center;
        height: 400px;
        img {
            margin-top: 30px;
            height: 100%;
            width: 90%;
        }
    }
    .row {
        .detail-content {
            .detail-content-inform {
                .content-inform-header {
                    text-align: center;
                    font-size: $fontSizeHeader;
                    text-transform: uppercase;
                    font-weight: 600;
                    padding: 10px 0;
                    margin-top: 30px;
                    color: rgb(16, 183, 0);
                }
                .content-inform-data {
                    display: flex;
                    flex-wrap: wrap;
                    padding: 10px 20px;
                    .inform-data-location {
                        width: 100%;
                        .inform-data-box {
                            display: flex;
                            justify-content: space-around;
                            div {
                                display: inline-block;
                                width: 50%;
                                .inform-data-icon {
                                    margin-right: 5px;
                                    color: #fff;
                                    font-size: $fontSizeHeader;
                                }
                                .inform-data-status {
                                    float: right;
                                    padding: 5px 10px;
                                    color: #fff;
                                    font-size: $fontSize;
                                }
                                .inform-data-address {
                                    color: #fff;
                                    font-weight: 600;
                                    font-size: $fontSize;
                                }
                            }
                        }
                    }

                    .inform-data-content {
                        display: block;
                        width: 100%;
                        text-align: center;
                        .data-content-container {
                            margin: 12px 12px;

                            display: flex;
                            flex-wrap: wrap;
                            gap: 30;
                            justify-content: space-between;
                            div {
                                padding: 10px 5px;
                                width: 45%;
                                display: inline-flex;
                                justify-content: space-between;
                                .data-content-title {
                                    font-size: 16px;
                                    font-weight: 700;
                                    color: #fff;
                                }
                                .data-content-text {
                                    color: #fff;
                                }
                                .red-content {
                                    color: rgb(255, 0, 0);
                                    font-style: italic;
                                    font-weight: 600;
                                    font-size: 12px;
                                    opacity: 0.8;
                                }
                            }
                        }
                    }
                }
            }

            .detail-content-location {
                .content-location-name {
                    font-size: $fontSizeHeader;
                    text-transform: uppercase;
                    font-weight: bold;
                    margin: 20px 0 10px;
                    padding-bottom: 10px;
                    border-bottom: 2px solid #00aaff;
                    color: #00aaff;
                }
                .location-content {
                    line-height: 2;
                    text-align: justify;
                    color: #fff;
                }
                div {
                    color: #fff;
                    margin-bottom: 10px;
                    margin-right: 20px;
                    margin-left: 20px;

                    .image-container {
                        text-align: center;
                        width: 100%;
                        height: 50vh; // Gii hn chiu cao ti a
                        object-fit: cover;
                        margin: 20px auto;

                        img {
                            width: 80%;
                            height: 100%; // Khng vt qu chiu cao container
                        }
                    }

                    // Responsive cho Mobile
                    @media (max-width: 768px) {
                        margin-right: 10px;
                        margin-left: 10px;

                        .image-container {
                            min-height: 25vh; // Gim chiu cao trn mn nh hn
                            max-height: 40vh;

                            img {
                                width: 90%; // M rng nh trn mobile
                            }
                        }
                    }

                    @media (max-width: 480px) {
                        margin-right: 5px;
                        margin-left: 5px;

                        .image-container {
                            min-height: 20vh;
                            max-height: 35vh;
                            text-align: center;

                            img {
                                width: 100%; // nh full chiu rng trn in thoi nh
                            }
                        }
                    }
                }

                margin-bottom: 15px;
            }

            .extension-container {
                .extension-name {
                    font-size: $fontSizeHeader;
                    text-transform: uppercase;
                    font-weight: 500;
                    font-size: $fontSizeHeader;
                    text-transform: uppercase;
                    font-weight: bold;
                    margin: 20px 0 10px;
                    padding-bottom: 10px;
                    border-bottom: 2px solid #00aaff;
                    color: #00aaff;
                }
                .extension-content {
                    color: #fff;
                    line-height: 2;
                    text-align: justify;
                    .extention-image-container {
                        width: 100%;
                        margin: 20px 20px;
                        text-align: center;
                        img {
                            width: 70%;
                            height: auto;
                        }
                    }
                    @media (max-width: 768px) {
                        margin-right: 10px;
                        margin-left: 10px;

                        .extention-image-container {
                            min-height: 25vh; // Gim chiu cao trn mn nh hn
                            max-height: 40vh;

                            img {
                                width: 90%; // M rng nh trn mobile
                            }
                        }
                    }

                    @media (max-width: 480px) {
                        margin-right: 10px;
                        margin-left: 5px;

                        .extention-image-container {
                            min-height: 20vh;
                            max-height: 35vh;

                            img {
                                width: 100%; // nh full chiu rng trn in thoi nh
                            }
                        }
                    }
                }
                .center-text {
                    text-align: center;
                    font-size: 14px;
                    opacity: 0.8;
                    font-style: italic;
                    margin-bottom: 20px;
                }
            }
        }
    }
}
